.modal {
  width: 640px;
  z-index: 3;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  margin: auto;
  padding: 0px 40px 60px;
  border-radius: 40px;
  background-color: #1c1c21;
}

.close {
  cursor: pointer;
  position: absolute;
  top: 60px;
  right: 40px;
}
